footer {
    height: 65px;
    background-color: $bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $tool;
    color: $white;
    // border-top: 1px solid $tool;

    h5 {
        font-size: 16px;
        font-weight: 300;
    }

    span {
        color: $tool;
        margin: -7px 0;
    }
}

@media (min-width: 641px) {
    footer {
        height: 35px;
        flex-direction: row-reverse;
        border: none;

        h5 {
            font-size: 16px;
            font-weight: 300;
        }
        span {
            color: $bg;
            margin: 0 25px;
        }
    }
}
