header {
    // position: relative;
    // top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75px;
    background-color: $header;
    box-shadow: $shadow-bottom;
    // border-bottom: 1px solid $tool;

    .img-container {
        height: 50px;
        transform: translateY(5px);
        cursor: pointer;

        img {
            height: 50px;
            object-fit: contain;
        }
    }
}

@media (min-width: 641px) {
    header {
        height: 100px;
        .img-container {
            height: 75px;
            transform: translateY(7px);
            img {
                height: 75px;
                // object-fit: contain;
            }
        }
    }
}
