.filter {
    width: 300px;
    margin: 25px 0;
    gap: 5px;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    direction: ltr;

    & > * {
        cursor: pointer;
        padding: 5px 10px;
        border: 1px solid $brand;
        border-radius: 100px;
        font-size: 14px;
    }

    .selected {
        background-color: $brand;
        color: $white;
        box-shadow: $shadow-active;
        font-weight: 600;
    }
}

@media (min-width: 641px) {
    .filter {
        width: 850px;
        margin: 50px 0;
        gap: 15px;
        display: flex;
        flex-direction: row-reverse;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        & > * {
            cursor: pointer;
            padding: 10px 20px;
            font-size: 16px;

            transform: scale(100%);
            transition: all ease-in 0.2s;

            &:hover {
                transform: scale(110%);
                transition: all ease-in 0.2s;
                color: $brand;
                box-shadow: $shadow;
                font-weight: 500;
            }
        }

        .selected {
            color: $white;
            font-weight: 600;
            box-shadow: $shadow-active;

            &:hover {
                transform: scale(100%);
                cursor: default;
            }
        }
    }
}
