.movie-details {
    flex-grow: 1;
    direction: rtl;
    background-color: $bg;
    // z-index: 200;
    // height: 100vh;
    // overflow-y: auto;
    // height: calc(100vh - 145px);
    // width: 100vw;
    // position: fixed;
    padding: 25px;
    overflow-y: auto;
    // overflow: hidden;

    display: flex;
    flex-direction: column-reverse;
    gap: 25px;

    .movie-container {
        // flex-basis: 50%;
        // overflow: hidden;
        // align-self: flex-start;
        // position: relative;
        // height: fit-content;

        .img-container {
            img {
                width: 100%;
                aspect-ratio: 16/9;
                object-fit: cover;
            }
        }

        .video-container {
            // width: 100%;
            // height: 100%;
            iframe {
            }
        }
    }

    .info {
        display: flex;
        flex-direction: column;

        h2.title {
            font-size: 22px;
            color: $brand;
            margin-bottom: 4px;
        }

        span.length {
            // display: none;
        }

        .catagories {
            // flex-grow: 1;
            display: flex;
            flex-wrap: wrap;
            // align-items: flex-start;
            gap: 5px;

            margin: 15px 0;

            & > * {
                // border: 1px solid $tool;
                // border: 1px solid $brand-light;
                background-color: $txt;
                color: $white;
                border-radius: 100px;
                padding: 5px 10px;
                font-size: 14px;
                // color: $white;
                // font-weight: 100;
            }
        }

        .des {
            // flex-grow: 1;
            display: flex;
            flex-direction: column;
            gap: 7px;

            p.full-des {
                // margin-bottom: 15px;
                font-size: 16px;
                line-height: 1.35;
                // font-weight: 200;
                // color: $white;
            }

            .credits {
                p.credit {
                    // font-size: 14px;
                }
            }
        }

        .btns-container {
            // position: relative;
            display: flex;
            justify-content: center;
            gap: 25px;

            margin-top: 50px;

            button {
                padding: 10px 15px;
                font-size: 16px;
                border-radius: 7px;
            }

            .watch {
                border: none;
                background-color: $brand;
                color: $white;
            }

            .back {
                background: transparent;
                border: 1px solid $brand;
                color: $brand;
            }
        }
    }
}

@media (min-width: 641px) {
    .movie-details {
        direction: rtl;
        background-color: $bg;
        // z-index: 200;
        height: calc(100vh - 135px);
        width: 100vw;
        // position: fixed;
        padding: 100px 200px;
        // overflow: hidden;

        display: flex;
        flex-direction: row;
        gap: 150px;

        .info {
            flex-basis: 50%;
            height: 100%;

            display: flex;
            flex-direction: column;
            // justify-content: space-between;
            // gap: 25px;

            h2.title {
                font-size: 25px;
                color: $brand;
                margin-bottom: 7px;
            }

            span.length {
                display: block;
                font-weight: 300;
                //     margin-bottom: 25px;
            }

            .catagories {
                // flex-grow: 1;
                display: flex;
                flex-wrap: wrap;
                // align-items: flex-start;
                gap: 10px;
                margin: 35px 0;

                & > * {
                    // border: 1px solid $tool;
                    // border: 1px solid $brand-light;
                    background-color: $txt;
                    color: $white;
                    border-radius: 100px;
                    padding: 5px 10px;
                    font-size: 16px;
                    // color: $white;
                    // font-weight: 100;
                }
            }

            .des {
                // flex-grow: 1;
                display: flex;
                flex-direction: column;
                // gap: 25px;

                p.full-des {
                    // margin-bottom: 15px;
                    font-size: 17px;
                    line-height: 1.35;
                    // font-weight: 200;
                    // color: $white;
                }

                .credits {
                    p.credit {
                        // font-size: 14px;
                    }
                }
            }

            .btns-container {
                display: flex;
                gap: 25px;
                justify-content: flex-start;

                margin-top: 50px;

                button {
                    padding: 10px 15px;
                    font-size: 16px;
                    border-radius: 7px;
                }

                .watch {
                    border: none;
                    background-color: $brand;
                    color: $white;
                }

                .back {
                    background: transparent;
                    border: 1px solid $brand;
                    color: $brand;
                }
            }
        }

        .movie-container {
            flex-basis: 50%;
            overflow: hidden;
            align-self: flex-start;
            position: relative;
            height: fit-content;

            .img-container {
                img {
                    width: 100%;
                    aspect-ratio: 16/9;
                    object-fit: cover;
                }
            }

            .video-container {
                // width: 100%;
                // height: 100%;
                iframe {
                }
            }
        }
    }
}
