.movies-main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;

    .movie-list {
        display: flex;
        flex-direction: column;
        padding-inline-end: 0;
        margin-bottom: 50px;
        gap: 40px;

        direction: ltr;

        button.scroll {
            background: transparent;
            border: 1px solid $txt;
            color: $brand;
            padding: 5px 10px;
            font-size: 16px;
            border-radius: 7px;
        }
    }
}

@media (min-width: 641px) {
    .movies-main {
        height: calc(100vh - 135px);
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;

        .movie-list {
            margin-bottom: 0px;
            width: 1000px;
            padding: 25px 30px 0 0;
            margin-bottom: 50px;
            // padding-top: 25px;
            display: flex;
            flex-direction: row-reverse;
            // padding-inline-end: 30px;
            flex-wrap: wrap;
            gap: 50px 25px;
            overflow-y: auto;

            button.scroll {
                display: none;
            }
        }
    }
}
