.home {
    display: flex;
    flex-direction: column;
    align-items: center;

    header {
        width: 100%;
        height: 90vh;
        padding: 150px 0;

        display: flex;
        flex-direction: column;

        .img-container {
            height: 200px;
            margin-bottom: 100px;

            img {
                height: 100%;
                object-fit: contain;
            }
        }

        .headline {
            text-align: center;

            h1 {
                font-size: 100px;
                color: $brand;
                margin-bottom: 25px;
                font-family: os-bold, sans-serif;
            }

            h2 {
                font-size: 75px;
                color: $white;
                font-family: os-light, sans-serif;
                font-weight: 100;

                span {
                    font-family: os-bold, sans-serif;
                    color: $brand;
                    letter-spacing: 2px;
                    margin-inline-end: 20px;
                }
            }
        }
    }

    .info {
        width: 100%;
        height: 90vh;
        display: flex;
        flex-direction: row;

        .img-container {
            // flex-grow: 2;
            width: 55%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;

            }
        }

        .text {
            // flex-grow: 1;
            width: 45%;
            padding: 175px;
            text-align: center;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;

            font-size: 30px;

            .text-block {
                display: flex;
                flex-direction: column;
                gap: 35px;

                p {
                    width: 40ch;
                    font-family: os-light;
                }

                span {
                    color: $brand;
                    // margin: 35px;
                    font-family: os-bold;
                    font-size: 40px;
                    text-shadow: $shadow;
                }
            }

            .btns {
                display: flex;
                gap: 100px;

                & > * {
                    width: 250px;
                    line-height: 1.75;
                }

                .btn-signup {
                    font-size: 32px;
                    color: $white;
                    background-color: $brand;
                    font-family: os-bold;
                }

                .btn-login {
                    font-size: 26px;
                    background: transparent;
                    border: 2px solid $txt;
                    color: $txt;
                }
            }
        }
    }

    .movie-month {
        background-color: $brand;

        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 100px 250px;

        h2 {
            font-family: os-semi;
            color: $tool;
            font-size: 40px;
            margin-bottom: 35px;
        }

        h3 {
            font-family: os-bold-italic;
            color: $white;
            font-size: 65px;
            margin-bottom: 50px;
            text-shadow: $shadow;
        }

        .movie-container {
            border: 3px solid $white;
            background-color: $brand-light;
            padding: 50px;
            display: flex;
            flex-direction: row;
            gap: 50px;
            box-shadow: $shadow;
            // padding: 0 100px;

            .movie-month-preview {
                // flex-basis: 1;
                width: 50%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: 100px;

                .movie-des {
                    color: $tool;
                    padding: 25px;
                    text-align: center;
                    font-size: 24px;
                    width: 50ch;
                }

                .btn-watch {
                    background-color: $txt;
                    color: $white;
                    width: 200px;
                    line-height: 2;
                    font-size: 24px;
                    border-radius: 7px;
                    border: none;
                    font-size: 24px;
                }
            }

            .img-container {
                // flex-basis: 1;
                width: 50%;

                img {
                    width: 100%;
                    object-fit: contain;
                }
            }
        }
    }

    .about-us {
        // border: 2px solid $tool;
        box-shadow: $shadow;
        background-color: $brand-opc;
        margin: 200px 0 150px;
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 50px;
        padding: 75px 100px;

        h3 {
            color: $brand;
            font-size: 50px;
            font-family: os-light;
        }

        h4 {
            font-size: 35px;
            color: $tool;
            font-family: os-bold;
        }

        p {
            font-size: 30px;
            line-height: 1.5;
            width: 65ch;
            // color: $white;

            span {
                font-family: os-bold;
            }
        }

        .social {
            display: flex;
            justify-content: center;
            gap: 25px;

            svg {
                cursor: pointer;
                fill: $tool;
                height: 100px;
            }
        }
    }

    .call-to-action {
        display: flex;
        flex-direction: column;
        gap: 50px;
        // padding: 100px;
        margin-bottom: 100px;

        h4 {
            color: $brand-light;
            font-size: 40px;
        }

        .btn-border {
            border-radius: 100px;
            border: 3px solid $brand;
            padding: 1px;
            margin: 0 auto;

            .btn-call {
                // width: 250px;
                line-height: 2;
                font-size: 40px;
                border-radius: 100px;
                border: none;
                background-color: $brand;
                color: $white;
                font-weight: 600;
                padding: 10px 50px;
            }
        }
    }

    footer {
        height: 75px;
        width: 100%;
    }
}
