.movie-preview {
    display: flex;
    flex-direction: column;
    align-self: center;
    gap: 10px;
    // cursor: pointer;
    width: 300px;
    box-shadow: $shadow;

    .img-container {
        height: 150px;

        img {
            width: 100%;
            object-fit: cover;
        }
    }

        h3 {
        color: $brand;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
    }

    p {
        padding: 0 15px 15px;
        color: $txt;
        font-size: 14px;
        text-align: center;
    }


}

@media (min-width: 461px) {
    .movie-preview {
        display: flex;
        align-self: flex-start;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 75px;
        cursor: pointer;
        width: 300px;
        box-shadow: $shadow;

        position: relative;

        &:hover {
            margin-bottom: 0;

            h3 {
                margin-bottom: 0px;
            }

            p {
                padding: 0 15px;
                display: block;
                color: $txt;
                font-size: 14px;
                text-align: center;
                padding-bottom: 15px;
            }
        }

        .img-container {
            height: 150px;

            img {
                width: 100%;
                object-fit: cover;
            }
        }

        h3 {
            color: $brand;
            font-size: 18px;
            font-weight: 600;
            text-align: center;
            margin-bottom: 15px;
        }

        .short-des {
            display: none;
        }
    }
}
